<template>
    <div>
        <app-header></app-header>
        <div class="left-side-block"></div>
        <div id="map" class="map">
            <iframe src="http://yandex.ru/map-widget/v1/?um=constructor%3Aa02ac7f52d0f38c49523506839cd4a9f51315128569b2d6a05b6e432d84c28f7&amp;source=constructor" width="617" height="457" frameborder="0"></iframe>
        </div>
        <div class="right-side-block"></div>
    </div>
</template>
<script>
import AppHeader from './main/Header.vue';
export default {
    data() {
        return {
            
        }
    },
    components: {
        AppHeader
    },
    methods: {

    },
    mounted() {

    }
}
</script>

<script type="text/javascript">
  var myMap;
  ymaps.ready(init); // Ожидание загрузки API с сервера Яндекса
  function init () {
    myMap = new ymaps.Map("map", {
	  apiKey: '9b29614f-8f0b-4b79-a9e9-c837c4101f71',
      center: [55.76, 37.64], // Координаты центра карты
      zoom: 10 // Zoom
    });
  }
</script>

<style scoped>
.left-side-block, .right-side-block {
    display: inline-block;
    position: absolute;
    width: 25%;
    height: 100%;
}
.map {
    position: absolute;
    left: 25%;
    width: 50%;
    height: 100%;
}
.left-side-block {
    float: left;
}
.right-side-block {
    right: 0;
}
</style>


